.wrapper {
    height: 747px;
    margin-bottom: 65px;
}
.zIndex {
    position: absolute;
    z-index: 100;
}
.gradient {
    position: absolute;
    z-index: -1;
    top: 0px;
    left: -116px;
    width: 978px;
    height: 737px;
    flex-shrink: 0;
    border-radius: 0px 0px 35px 35px;
    background: var(--Gradient-1, linear-gradient(290deg, #D6E6FE 0%, rgba(214, 252, 254, 0.00) 100%));
}
.logo {
    margin-top: 75px;
}
.disc {
    width: 64px;
    height: 64px;
}
.wrap {
    width: 564px;
}
.title {
    margin-bottom: 35px;
}
.descr {
    color: var(--Dark-Electric-Blue, #5E6E85);
    width: 465px;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}
.flexPage {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    justify-content: center;
    align-items: center;
}

/* FORM */

.wrapForm {
    display: flex;
    width: 564px;
    padding: 32px;
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    border-radius: 16px;
    background: var(--Pure-White, #FFF);
    box-shadow: 16px 32px 56px 0px rgba(143, 174, 207, 0.25);
}
.titleForm {
    color: var(--Gunmetal, #253347);
}
.flex, .flexMetric {
    display: flex;
    gap: 24px;
}

.height,
.weight {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 238px;
}
.label {
    color: var(--Gunmetal, #253347);
}
.mail {
    color: #fff;
}
input  { 
    margin: 0;
    display: flex;
    padding: 20px 24px; 
    align-items: flex-start;
    gap: 24px;
    align-self: stretch;
    border-radius: 12px;
    border: 1px solid #D8E2E7;
}
.InputNumber {
    margin-top: 8px;
    padding: 20px 24px; 
    width: 190px ;
    font-size: 20px;
    font-weight: 600;
}
.inputNumber:focus {
    outline: none;
    border: 1px solid var(--Blue, #345FF6);
    transition: .3s all;
    box-shadow: 1px 2px 15px blue;
}
.inputNumber:hover {
    box-shadow: 1px 1px 6px blue;
    transition: .3s all;
}
.flexImperial {
    display: flex;
    gap: 24px;

}
.MI {
    display: flex;
    width: 238px;
    gap: 18px;
    align-items: center;
    text-align: center;
}
.radio {
    width: 31px;
    height: 31px;
}
.radio:hover {
    stroke: var(--Blue, #345FF6);
}
.measure {
    position: absolute;
    right: 20px;
    top: 47px;
    color: var(--Blue, #345FF6);
}

.flexInput {
    display: flex;
    gap: 24px;
    justify-content: space-between;
    color: var(--Gunmetal, #253347);
}
.slInpt {
    position: relative;
}
.measureImp {
    position: absolute;
    right: 20px;
    top: 24px;
    color: var(--Blue, #345FF6);
    
}
.titleForm,
.measure,
.measureImp,
.result__title, .btn {
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -1.2px;
}
.btn {
font-size: 16px;
color: var(--Blue, #345FF6);
border: solid 1px var(--Blue, #345FF6);
border-radius: 15px;
padding: 5px 10px;
background: transparent;
text-transform: uppercase;
justify-content: flex-end;
margin-left: 420px;
cursor: pointer;
}
.btn:hover {
    box-shadow: 5px blue;
}

/* RESULT */

.result, .wrapResult {
    width: 440px;
    padding: 32px;
    flex-direction: column;
    justify-content: center;
    align-self: stretch;
    border-radius: 16px 999px 999px 16px;
    background: linear-gradient(90deg, #345FF6 0%, #587DFF 100%)
}

.result__title {
    color: var(--Pure-White, #FFF);
    margin-bottom: 16px;    
}
.result__descr {
    color: var(--Pure-White, #FFF);
}

.wrapResult { 
    display: flex;
    flex-direction: row;
}
.item {
    width: 206px;
}
.name {
    color: var(--Pure-White, #FFF);
}
.label, .name {
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; 
}
.number {
    color: var(--Pure-White, #FFF);
}
.title, .number {
    font-family: Inter;
    font-size: 64px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; 
    letter-spacing: -3.2px;
}
.expl {
    width: 206px;

    color: var(--Pure-White, #FFF);
}
.result__descr, .expl {
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

}
.span {
    font-weight: 700;
}
.mail__footer {
    color: black;
}




/* MEDIA */


@media (min-width: 1200px) {
    .container {
        width: 1060px;
    }
 }
 @media (max-width: 1200px) {
    .container {
        width: 90%;
    }
    .gradient {
        left: 0;
        width: 100%;
    }
    .logo {
        text-align: center;
        margin: 25px 0;
    }
    .title, .descr {
        text-align: center;
    }
    .wrap {
        width: 100%;
        text-align: center;
    }
    .descr {
        width: 100%;
    }
    
  }
@media (max-width: 990px) { 
    .wrap {
        width: 80%;
    }

}

@media (max-width: 768px) {
    .wrapForm {
        width: 80%;
        gap: 25px;
        padding: 20px
    }
    .result {
        width: 90%;
    }
    .wrapper {
        margin-bottom: 126px;
    }
    .title {
        font-size: 45px;
        margin-bottom: 20px;
    }
    .titleForm {
        font-size: 20px;
    } 
    .label {
        width: 40%;
    }
    .InputNumber {
        padding: 15px 20px;
        width: 50%;
    }
    .btn {
        margin-left: 0;
    }
    .wrapResult {
        width: 90%;
        padding: 15px;
    }
    .measure {
        right: 42px;
    }
    .flex {
        width: 100%;
    }
} 
@media (max-width: 575px) {
    .zIndex {
        position: relative;
    }
    .title {
        font-size: 37px;
    }
    .descr {
        font-size: 15px;
    }
    .result {
        width: 90%;
        padding: 13px;
    }
    .result__title {
        font-size: 21px;
    }
    .result__descr {
        font-size: 12px;
    }
    .InputNumber {
        width: 40%;
        padding: 7px 7px;
    }
    .measure {
        right: 17px;
        top: 35px;
    }
    .weight, .height {
        width: 100%;
    }
    .wrapResult {
        padding: 10px;
        flex-direction: column;
    }
    .item, .expl {
        width: 100%;
        font-size: 12px;
    }
    .name {
        font-size: 14px;
        margin-bottom: 7px; 
    }
    .number {
        font-size: 35px;
    }
    .item {
        flex-direction: row;
        display: flex;
        gap: 20px;
        align-items: center;
    }
}
@media (max-width: 345px) {
    .wrap {
        width: 100%;
    }
}
